const choices = ['rock', 'paper', 'scissors'];
let playerSelection = getPlayerChoice();
let computerSelection = getComputerChoice();
let userScore = 0;
let computerScore = 0;

function getComputerChoice() {
  computerSelection = choices[Math.floor(Math.random() * choices.length)];
  return computerSelection;
}

function getPlayerChoice() {
  playerSelection = prompt('Please make your selection').toLowerCase;
  return playerSelection;
}

function playRound() {
  // messages to be returned to user at the end of the round
  const winMsg = `You win the round! ${playerSelection} beats ${computerSelection}`;
  const loseMsg = `You lose the round! ${computerSelection} beats ${playerSelection}`;
  const drawMsg = `Tie round. You both chose ${playerSelection}`;

  if (computerSelection === 'rock' && playerSelection === 'paper') {
    userScore += 1;
    return winMsg;
  }
  if (computerSelection === 'rock' && playerSelection === 'scissors') {
    computerScore += 1;
    return loseMsg;
  }
  if (computerSelection === 'paper' && playerSelection === 'scissors') {
    userScore += 1;
    return winMsg;
  }
  if (computerSelection === 'paper' && playerSelection === 'rock') {
    computerScore += 1;
    return loseMsg;
  }
  if (computerSelection === 'scissors' && playerSelection === 'rock') {
    userScore += 1;
    return winMsg;
  }
  if (computerSelection === 'scissors' && playerSelection === 'paper') {
    computerScore += 1;
    return loseMsg;
  }

  return drawMsg;
}
